<script>
export default {
  name: 'left-select',
  components: {},
  data () {
    return {
      system: false,
      display: 0,
      display_left: 0,
      display_left22: 0,
      display_left33: 0
    }
  },
  created () {
  },
  methods: {
    handleSelect (key, keyPath) {
      var _this = this
      switch (key) {
        case '1-1':
          if (this.$route.path !== '/collection'){
            _this.$router.push('/collection')
          }
          break
        case '2-1':
          if (this.$route.path !== '/payCollectionChild') {
            _this.$router.push('/payCollectionChild')
          }
          break
        case '2-2':
          if (this.$route.path !== '/payEducationContinue') {
            _this.$router.push('/payEducationContinue')
          }
          break
        case '2-3':
          if (this.$route.path !== '/payHouseInterest') {
            _this.$router.push('/payHouseInterest')
          }
          break
        case '2-4':
          if (this.$route.path !== '/payHouseRent') {
            _this.$router.push('/payHouseRent')
          }
          break
        case '2-5':
          if (this.$route.path !== '/payOldman') {
            _this.$router.push('/payOldman')
          }
          break
        case '3':
          if (this.$route.path !== '/declaration') {
            _this.$router.push('/declaration')
          }
          break
        case '4':
          _this.showNull()
          break
        case '5':
          _this.showNull()
          break
        case '6':
          _this.showNull()
          break
        case '7-1':
          if (this.$route.path !== '/taxThree') {
            _this.$router.push('/taxThree')
          }
          break
        case '7-2':
          if (this.$route.path !== '/taxHistory') {
            _this.$router.push('/taxHistory')
          }
          break
        case '8-1':
          _this.showNull()
          break
        case '8-2':
          _this.showNull()
          break
        case '8-3':
          _this.showNull()
          break
        case '8-4':
          _this.showNull()
          break
        case '9-1':
          if (this.$route.path !== '/selectCompany') {
            _this.$router.push('/selectCompany')
          }
          break
        case '9-2':
          if (this.$route.path !== '/selectPerson') {
            _this.$router.push('/selectPerson')
          }
          break
        case '10':
          this.showSystem()
          break
      }
    },
    showNull () {
      this.$alert('本系统为教学系统，目前只提供常用操作功能噢', '信息', {
        confirmButtonText: '确定'
      })
    },
    showSystem () {
      this.system = true
    },
    displayClick (num) {
      this.display = num
    },
    display_left1 (num) {
      this.display_left = num
    },
    display_left2 (num) {
      this.display_left22 = num
    },
    display_left3 (num) {
      this.display_left33 = num
    }

  }
}
</script>

