<template>
  <div class="Hui-article-box" style="min-width:800px;position:relative">
    <div id="Hui-tabNav" class="Hui-tabNav hidden-xs">
      <div class="Hui-tabNav-wp">
        <ul id="min_title_list" class="acrossTab cl">
          <router-link to="/index">
            <li class="active">
              首页
              <em></em>
            </li>
          </router-link>
        </ul>
      </div>
      <div class="Hui-tabNav-more btn-group" style="display: none;">
        <a id="js-tabNav-prev" class="btn radius btn-default size-S" href="javascript:;">
          <i class="Hui-iconfont"></i>
        </a>
        <a id="js-tabNav-next" class="btn radius btn-default size-S" href="javascript:;">
          <i class="Hui-iconfont"></i>
        </a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'page-content',
  props: ['top_li'],
  components: {},
  created () {
  },
  data () {
    return {}
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  .Hui-article-box {
    width: 100%;
    height: 35px;
  }

  .Hui-tabNav-wp {
    width: 100%;
    position: relative;
    height: 35px;
    overflow: hidden;
  }

  .Hui-tabNav {
    height: 35px;
    padding-right: 75px;
    overflow: hidden;
    position: relative;
    background: #efeef0 url(../../../static/img/acrossTab-2.png) repeat-x 0 -175px;
  }

  .Hui-tabNav .acrossTab {
    position: absolute;
    height: 26px;
    line-height: 26px;
    background: none;
    left: 0;
    bottom: 0;
    padding-top: 0;
  }

  .acrossTab li {
    background-image: url(../../../static/img/acrossTab-2.png);
    position: relative;
    display: inline-block;
    height: 29px;
    line-height: 29px;
    font-size: 12px;
    cursor: pointer;
    padding: 0 30px;
    margin: 0;
    white-space: nowrap;
    color: #282828;
    background-position: 0 0;
  }

  .acrossTab li em {
    position: absolute;
    width: 23px;
    height: 29px;
    right: -18px;
    top: 0;
    z-index: 50;
    background-position: right -30px;
    background-image: url(../../../static/img/acrossTab-2.png);
  }

  .acrossTab li i {
    position: absolute;
    display: block;
    width: 13px;
    height: 13px;
    top: 50%;
    margin-top: -6px;
    right: 5px;
    font-size: 0;
    line-height: 0;
    cursor: pointer;
    background: url(../../../static/img/acrossTab-close.png) no-repeat 0 0;
  }
</style>



