<template>
  <div style="width: 100%;height: 100%;display: flex;flex-direction: column">
    <topHead></topHead>
    <div style="flex: 1;display:flex">
      <leftSelect></leftSelect>
      <div style="width:calc(100% - 200px); padding-bottom:20px">
        <pageContent top_li="aaa"></pageContent>
        <router-view></router-view>
      </div>
    </div>
    <div
      style="position: fixed;height: 25px;line-height: 25px;bottom: 0px;width: 100%;background-color: #EAEDEF; text-align:start;font-size:14px;color:black"
    >
      <span style="margin-left: 20px">
        <i class="Hui-iconfont Hui-iconfont-user" style="font-size: 15px"></i> 当前操作的企业:
      </span>
      <span>{{company[0].name}} [{{company[0].taxpayerNumber}}]</span>
    </div>

  </div>
</template>

<script>
import topHead from './components/top-head'
import leftSelect from './components/left-select'
import pageContent from './components/page-content'
import { mapGetters } from 'vuex'

export default {
  components: {
    topHead,
    leftSelect,
    pageContent
  },
  data () {
    return {
      pop: true,
      doNext: false,
      number: 0
    }
  },
  computed: {
    ...mapGetters(['company'])
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang='scss' scoped>
  img {
    border: 0;
    vertical-align: middle;
  }

  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .business-bottom {
    box-sizing: border-box;
    width: 100%;
    position: absolute;
    bottom: 0px;
    height: 43px;
    text-align: center;
    line-height: 42px;
    border-top: 1px solid #eeeeee;
  }

  .do-next {
    width: 113px;
    height: 75px;
    background: url(../../static/img/do_next.png);
    position: fixed;
    right: -45px;
    bottom: 100px;
    cursor: pointer;
    z-index: 100;
  }

  .business-content {
    text-align: start;
    padding: 20px;
  }
</style>



