        [教学系统]
        <br/>
        <span style="font-size: 12px;color: #CA9444;float: right">[2019年新税制]</span>
      </span>
      <div class="f-l daikou">
        <router-link to="/">
          <div>
            <img src="../../../static/img/btn_SBFSB3.png"/>
          </div>
          <div>代扣代缴</div>
        </router-link>
      </div>
    </div>
    <div class="top-pic-div">
      <span class="top-text notice">
        <img src="../../../static/img/top_czsc_1.png"/>
