        <span>操作手册</span>
      </span>
      <span class="top-text notice">
        <img src="../../../static/img/top_mail_1.png"/>
        <span>消息中心</span>
      </span>
      <span class="top-text" id="menu_firms" @click="qiyeguanli">
        <img src="../../../static/img/company.png" style="width: 20px;height: 20px;margin-top: -1px"/>
        <span>企业管理</span>
      </span>
      <span class="top-text" id="exit" @click="clear">
        <i class="Hui-iconfont Hui-iconfont-del2" style="font-size: 18px"></i>
        <span>清空数据</span>
      </span>
    </div>
    <el-dialog
      class="in-table-system"
      title="境内人员信息"
      :visible.sync="qi"
      width="826px"
      height="443px"
      top="25vh"
    >
      <div
        class="layui-layer-title"
        style="color: rgb(51, 51, 51); background-color: rgb(255, 255, 255); border: 1px solid rgb(142, 142, 142); "
        move="ok"
      >单位管理
      </div>

      <a
        class="layui-layer-ico layui-layer-close layui-layer-close1"
        @click="qi = false"
        href="javascript:;"
        style="width:15px;height:15px;position:absolute;top:15px;right:15px;background-position: 0 -40px;"
      ></a>
      <div id="chose_firm_content">
        <!--top查询-->
        <div class="firms-top">
          <!--纳税人识别号-->
          <input type="text" class="input-text size-MINI" id="firm_code"/>
          <!--纳税人名称-->
          <input type="text" class="input-text size-MINI" id="firm_name"/>
          <!--查询按钮-->
          <a class="btn btn-primary radius menu-btn" type="button" id="pic_search">查询</a>
          <!--图片-->
          <div class="firm-pic">
            <input type="text" id="firm_all" :value="company.length"/>
            <input type="text" id="firm_have" :value="companyShenbao.length"/>
            <input type="text" id="firm_no" :value="companyWeishenbao.length"/>
          </div>
        </div>
        <div class="firm-mid">
          <!--添加按钮-->
          <button class="btn btn-primary radius menu-btn" id="pic_add" @click="addcompanyClick">添加</button>
          <!--删除按钮-->
          <button class="btn btn-primary radius menu-btn" id="pic_del" @click="deletea">删除</button>
        </div>
        <!--表格显示-->
        <div class="firm-bottom">
          <table
            class="table table-border table-bordered"
            id="firm_table"
            style="margin: -1px;width: 100%"
          >
            <tbody>
            <tr>
              <th style="width: 30px">序号</th>
              <th style="width: 210px">纳税人识别号</th>
              <th>纳税人名称</th>
              <th style="width: 100px">当月申报状态</th>
              <th style="width: 75px">备注</th>
              <th style="width: 80px">操作</th>
            </tr>
            <tr
              v-for="(item, i) in company"
              :key="i"
              style="cursor: pointer"
              @click="select(i,item.taxpayerNumber)"
              :class="item.isSelect?'active':''"
            >
              <td style="width: 30px">{{i+1}}</td>
              <td>{{item.taxpayerNumber}}</td>
              <td>{{item.name}}</td>
              <td>未申报</td>
              <td></td>
              <td style="display: none;">4695</td>
              <td>
                <button
                  type="button"
                  class="btn btn-secondary size-MINI firm-table-btn"
                  @click.stop="gocompany(item)"
                >进入
                </button>
              </td>
            </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div
        class="addcompany"
        v-if="addcompany"
        style="width:510px;height:335px;z-index:9999999;position:absolute;margin:auto;padding:auto;top:50%;left:50%;transform:translate(-50%,-50%)"
      >
        <div
          class="layui-layer-title"
          style="color: rgb(255, 255, 255); background-color: rgb(55, 104, 173); border-style: solid; border-color: rgb(142, 142, 142); border-image: initial; border-width: 2px 2px 0px; cursor: move;"
          move="ok"
        >
          <img
            src="/Public/AccountantOperate/img/set.png"
            style="height: 16px;line-height:16px;width: 18px;margin-right: 5px"
          />新增企业
        </div>
        <div id class="layui-layer-content" style="height: 291px;">
          <div id="firm_content" class="layui-layer-wrap" style="display: block;">
            <!--1-->
            <div id="firm_content1" style="display: block;">
              <div style="float:left;width: 100%">
                <input
                  class="input-text size-MINI firm1-input"
                  id="code"
                  type="text"
                  maxlength="20"
                  v-model="companyInfo.taxpayerNumber"
                  style="text-transform: uppercase"
                />

                <div id="help1"></div>

                <input
                  class="input-text size-MINI firm1-input"
                  id="code_confirm"
                  type="text"
                  maxlength="20"
                  v-model="secondTaxpayerNumber"
                  style="text-transform: uppercase"
                />

                <div id="help2"></div>
              </div>

              <div
                style="float: left;width: 15px;height: 15px;margin: 57px 0px 0px 6px;cursor: pointer;"
