              ></div>
              <div style="float: left;width: 100%;height: 50px;margin-top: 30px">
                <input
                  type="button"
                  class="btn btn-primary size-L"
                  id="firm1_save"
                  @click="addcompanyPush"
                  value="保存"
                />
                <input
                  type="button"
                  class="btn btn-primary size-L"
                  id="firm1_cancel"
                  value="取消"
                  @click="addcompanyCancle"
                />
