</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      qi: false,
      selectcompany: [],
      addcompany: false,
      secondTaxpayerNumber: ''
    }
  },
  computed: {
    ...mapGetters(['company']),
    companyShenbao () {
      let shenbaoArr = []
      for (let i in this.company) {
        if (this.company[i].isShenbao === '已申报') {
          shenbaoArr.push(this.company[i])
        }
      }
      return shenbaoArr
    },
    companyWeishenbao () {
      let weishenbaoArr = []
      for (let i in this.company) {
        if (this.company[i].isShenbao === '未申报') {
          weishenbaoArr.push(this.company[i])
        }
      }
      return weishenbaoArr
    }
  },
  methods: {
    clear () {
      this.$confirm('此操作将永久删除之前的操作, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.$message({
          type: 'success',
          message: '删除成功!'
        })
        localStorage.clear()
        this.$router.replace('/')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    qiyeguanli () {
      this.qi = true
    },
    select (i, taxpayerNumber) {
      if (this.company[i].isSelect) {
        let data = {}
        data.keyNumber = i
        data.key = 'isSelect'
        data.value = false
        this.$store.commit('updateOneNumbercompany', data)
      } else {
        let data = {}
        data.keyNumber = i
        data.key = 'isSelect'
        data.value = true
        this.$store.commit('updateOneNumbercompany', data)
      }
    },
    addcompanyCancle () {
      this.addcompany = false
    },
    deletea() {
      this.$message.warning('当前模式无法使用此功能')
    },
    addcompanyClick () {
      this.$message.warning('当前模式无法使用此功能')
      // this.addcompany = true
    },
    addcompanyPush () {
      // this.$store.commit('addcompany', this.companyInfo)
      // this.addcompany = false
      // this.companyInfo = this.$options.data().companyInfo
    },
    gocompany (data) {
      this.qi = false
      if (this.$route.path !== '/index') {
        this.$router.push('/index')
      }
    }
  }
}
</script>

